.home{
    height: 100vh;
    width: 100%;
    font-family: 'Courier New', Courier, monospace;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    transition: background-color 0.5s ease-in 0.1s;
    transition: border 0.5s ease-in 0.1s;
    transition: color 0.5s ease-in 0.1s;
    position: fixed;
}
.lightTheme{
    /* background: repeating-linear-gradient(
  180deg,
  #94c8a6,
  #94c8a6 20px,
  #acc8b6 20px,
  #acc8b6 25px
); */
    background-color:  #94c8a6;
    border-top: 3px solid #092a3a;
    border-bottom: 3px solid #092a3a;
    color: #092a3a;
}
.darkTheme{
    /* background: repeating-linear-gradient(
        180deg,
        #051821,
        #051821 20px,
        #092a3a 20px,
        #092a3a 25px
      ); */
    /* background-color: #051821; */
    background-color: #092a3a;
    border-top: 3px solid #94c8a6;
    color:#94c8a6;
}

.header{
    width: inherit;
    transition: box-shadow 0.3s ease-in 0.1s;
    transition: background 0.3s ease-in 0.1s;
    display: flex;
    justify-content: space-between;
    position: fixed;

}
.lightHeader{
    background-color:  #94c8a600;
    box-shadow: 0px 0px 20px #00000042;
}
.darkHeader{ 
    background-color: #092a3a00;
    box-shadow: 0px 0px 20px #0000006e;
}

.moon{
    color: #092a3a;
    margin-right: 15px;
    margin-top: 15px;
    cursor: pointer;
}
.sun{
    color: #94c8a6;
    margin-right: 15px;
    margin-top: 15px;
    cursor: pointer;
}

.mainLogo{
    margin: auto;
    height: 40px;
    margin-left: 10px;
    margin-top: 10px;
     margin-right: 10px;
}

.navs{
    display: flex;
    transition: color 0.5s color 0.1s;
    font-size: 20px;
}
.lightNavs{
    color: #092a3a;
}
.darkNavs{
    color: #94c8a6;
}
.navItem{
    margin: auto 5px auto 5px;
    cursor: pointer;
}
.navItem:hover{
    text-shadow: 1px 1px;
}
.welcome{
height: 100vh;
margin: auto 2px auto 2px;
}

.welcomeText{
   display: flex;
   flex-direction: column;
   width: 85%;
   height: 80%;
   max-height: 80%;
   overflow: scroll;
   margin: auto;
   margin-top: 90px;
   transition: box-shadow 0.5s ease-in 0.1s;
   border-radius: 5px;
   padding: 20px;
}
.lightWelcome{
    box-shadow: inset 0px 0px 20px #00000042;
}
.darkWelcome{
    box-shadow: inset 0px 0px 20px #0000006e
}

.photoContainer{
    display: flex;
    width: 50%;
    margin: auto;
    justify-content: center;
}
.photo{
    height: 200px;
    width: 200px;
    border-radius: 100px;
    transition: border 0.5s ease-in 0.1s;
    margin: auto;
}
.lightPhoto{
    box-shadow: 0px 0px 20px #00000042;
}
.darkPhoto{
    box-shadow: 20px 1px 50px #0000006e;
}

.greeting{
    font-weight: 900;
    width: 75%;
    margin: auto;
    padding: 10px;
    text-align: center;
}

.links{
    display: flex;
    justify-content: space-around;
    margin: auto;
}
.link{
    margin: 12px 5px 12px 5px;
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;
}
.link:hover{
    text-shadow: 1px 1px;
}
.downArrow{
    margin: auto;
    animation-duration: 2s;
    animation-iteration-count: 3;
}

.down-bounce{
    animation-name: bounce-2;
    animation-timing-function: ease;
}

@keyframes bounce-2 {
    0%   { transform: translateY(0); }
    50%  { transform: translateY(-20px); }
    100% { transform: translateY(0); }
}


.contact{
    margin: auto;
    flex-direction: column;
    display: flex;
    align-items: center;
    width: 50%;
}
.contactTag{
    
    width: fit-content;
    padding: 5px 15px;
    border-radius: 1px;
    box-shadow: 0px 0px 20px #00000042;
    font-weight: 600;
    text-transform: uppercase;
    transition: background 0.5s ease-in 0.1s;

}
.info{
    font-weight: 800;
    padding-top: 5px;
    padding-right: 5px;
}
.email{
    max-width: 100%;
    cursor: pointer;
    word-wrap:break-word;
}

.aboutMe{
    border-radius: 5px;
    box-shadow: 0px 0px 20px #00000042;
    width: 60%;
    margin: auto;
    padding: 17px;
    font-size: 15px;
    font-weight: 500;
    display: flex;
    text-align: center;
    transition: background 0.5s ease-in 0.1s;
}

.tagLight{
    background-color: #092a3a5c;
}
.tagDark{
    background-color:  #94c8a64c;
}


.projectsTitle{
    margin: auto;
    font-size: 25px;
    width: fit-content;
    padding: 5px 15px;
    border-radius: 5px;
    box-shadow: 0px 0px 20px #00000042;
    font-weight: 600;
    text-transform: uppercase;
    transition: background 0.5s ease-in 0.1s;
}

.projectBlock{
    width: 95%;
    display: flex;
    align-self: center;
    justify-content: space-evenly;
    
}
.projectGif{
    width: 45%;
  
}
.project{
    box-shadow: 0px 0px 20px #00000042;
    border-radius: 5px;
    margin: 20px;
   
}
@media only screen and (max-width: 1189px){
    .projectBlock{
        width: 95%;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: space-between;
        
    }
    .projectGif{
        width: 100%;
    }
}
.comingSoon{
    width: 100%;
    text-align: center;
    font-weight: 700;
    font-style: oblique;
    margin: 30px 0px 30px 0px
}
    

